.header {
  background-color: #333;
  color: #fff;
  padding: 15px 20px;
}
.header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header ul {
  list-style: none;
  display: flex;
  gap: 20px;
}
.header ul li a {
  color: #fff;
  text-decoration: none;
}
.header ul li a:hover {
  text-decoration: underline;
}
